                           <li>
                               <div class="inn-f">经度：</div>
                               <div class="inn-fN inn-fN1">
                                   <div>{{data.city_coordinate_j}}</div>
                                   <div>维度：</div>
                                   <div>{{data.city_coordinate_w}}</div>
                               </div>
                               <div class="inn-l">位置：</div>
                               <div class="inn-fN">{{data.city_name}}</div>
                           </li>
                           <li>
                               <div class="inn-f">机场状态：</div>
                               <div class="inn-fN" onclick="return false">
                                   <label onclick="return false">
                                       <input type="radio" name="sex"  :checked="status.chd[0]"><i></i>启用
