                           <li>
                               <div class="inn-f">航空口岸：</div>
                               <div class="inn-fN">
                                   <label onclick="return false">
                                       <input type="radio" name="birth" :checked="status.fhd[0]"><i></i>发布
                                   </label>
                                   <label onclick="return false">
                                       <input type="radio" name="birth" :checked="status.fhd[1]"><i></i>未发布
