                                   <label onclick="return false">
                                       <input type="radio" name="terrainDatabase" :checked="status.xhd[1]"><i></i>未发布生效
                                   </label>
                                   <label onclick="return false">
                                       <input type="radio" name="terrainDatabase" :checked="status.xhd[2]"><i></i>生效
                                   </label>
                               </div>
                           </li>
                       </ul>
                   </div>
                   <div class="innovation-minute">
                       <h4 class="innovation-blockT">机场交通</h4>
                       <ul class="innovation-block">
