                           </li>
                       </ul>
                   </div>
                   <div class="innovation-minute">
                       <h4 class="innovation-blockT">机场规模</h4>
                       <ul class="innovation-block">
                           <li>
                               <div class="inn-f">占地面积：</div>
                               <div class="inn-fN">
                                   <div class="inn-fNx">{{data.air_covers}}</div>
                                   <div class="inn-fNx1">㎡</div>
                               </div>
                               <div class="inn-l">航站楼：</div>
                               <div class="inn-fN">
                                   <div>
                                       <div  class="fNx2x">
                                           <div>{{data.air_terminal}}</div>
                                           <div>座</div>
                                       </div>
                                       <div class="fNx2">
                                           <div >机位总数：</div>
                                           <div class="fNx2x">
                                               <div>{{data.slots_total}}</div>
                                               <div>个</div>
                                           </div>
                                       </div>
                                   </div>
                               </div>
                           </li>
                           <li>
                               <div class="inn-f">候机楼：</div>
                               <div class="inn-fN">
                                   <div class="inn-fNx">{{data.terminal}}</div>
                                   <div class="inn-fNx1">个</div>
                               </div>
                               <div class="inn-l">登机桥：</div>
                               <div class="inn-fN">
                                   <div class="inn-fNx">{{data.air_boarding_bridge}}</div>
                                   <div class="inn-fNx1">个</div>
                               </div>
                           </li>
                           <li>
                               <div class="inn-f">停机坪：</div>
                               <div class="inn-fN">
                                   <div class="inn-fNx">{{data.air_apron}}</div>
                                   <div class="inn-fNx1">个</div>
                               </div>
                               <div class="inn-l">高峰小时容量：</div>
                               <div class="inn-fN">
                                   <div class="inn-fNx">{{data.air_capacity}}</div>
                                   <div class="inn-fNx1">架次（针对协调机场）</div>
                               </div>
                           </li>
                           <li>
                               <div class="inn-f">跑道：</div>
                               <div class="inn-fN">
                                   <div class="inn-fNx">{{data.air_runway}}</div>
                                   <div class="inn-fNx1">条</div>
                               </div>
                               <div class="inn-l">最优跑道：</div>
                               <div class="inn-fN inn-fNb">
                                   <div>
                                       <div  class="fNx3x">
                                           <div class="fNx3x-o">长</div>
                                           <div>{{data.air_runway_long}}</div>
                                           <div>m</div>
                                       </div>
                                       <div class="fNx3">
                                           <div></div>
                                           <div class="fNx3x">
                                               <div class="fNx3xs">&nbsp;&nbsp;宽</div>
                                               <div>{{data.air_runway_wide}}</div>
                                               <div>m</div>
                                           </div>
                                       </div>
                                   </div>
                               </div>
                           </li>
                       </ul>
                   </div>
                   <div class="innovation-minute">
                       <h4 class="innovation-blockT">机场历史运营</h4>
                       <ul class="innovation-block">
                           <li>
                               <div class="inn-f">上一年度完成起降：</div>
                               <div class="inn-fN">
                                   <div class="inn-fNx">{{data.air_complete}}</div>
                                   <div class="inn-fNx1">架</div>
                               </div>
                               <div class="inn-l">在飞航线：</div>
                               <div class="inn-fN">
                                   <div class="inn-fNx">{{data.air_line}}</div>
                                   <div class="inn-fNx1">条</div>
                               </div>
                           </li>
                           <li>
                               <div class="inn-f">旅客吞吐量：</div>
                               <div class="inn-fN">
                                   <div class="inn-fNx">{{data.air_Passenger_throughput}}</div>
                                   <div class="inn-fNx1">人次</div>
                               </div>
                               <div class="inn-l">国内在飞航线：</div>
                               <div class="inn-fN">
                                   <div class="inn-fNx">{{data.air_line_domestic}}</div>
                                   <div class="inn-fNx1">条</div>
                               </div>
                           </li>
                           <li>
                               <div class="inn-f">货邮吞吐量：</div>
                               <div class="inn-fN">
                                   <div class="inn-fNx">{{data.air_Goods_throughput}}</div>
                                   <div class="inn-fNx1">吨</div>
                               </div>
                               <div class="inn-l">国际在飞航线：</div>
                               <div class="inn-fN">
                                   <div class="inn-fNx">{{data.air_line_inter}}</div>
                                   <div class="inn-fNx1">条</div>
                               </div>
                           </li>
                       </ul>
                   </div>
                   <div class="innovation-minute">
                       <h4 class="innovation-blockT">建设计划</h4>
                       <ul class="innovation-block">
                           <li>
                               <div class="inn-f">初建日期：</div>
                               <div class="inn-fN">{{data.air_early_date}}</div>
                               <div class="inn-l">验收日期：</div>
                               <div class="inn-fN">{{data.air_acceptance_date}}</div>
                           </li>
                           <li>
                               <div class="inn-f">校飞日期：</div>
                               <div class="inn-fN">{{data.air_school_fly_date}}</div>
                               <div class="inn-l">试飞日期：</div>
                               <div class="inn-fN">{{data.air_flight_date}}</div>
                           </li>
                           <li>
                               <div class="inn-f">计划通航时间：</div>
                               <div class="inn-fN">{{data.air_Planned_shipping_time}}</div>
                               <div class="inn-l">目标吞吐量：</div>
                               <div class="inn-fN">
                                   <div class="inn-fNx">{{data.air_target_throughput}}</div>
                                   <div class="inn-fNx1">人次</div>
                               </div>
                           </li>
                           <li>
                               <div class="inn-f">初步规划城市：</div>
                               <div class="inn-fN">{{data.air_Preliminary_planning_and_city}}</div>
                           </li>
                           <li>
                               <div class="inn-f">奖励政策：</div>
                               <div class="inn-fN">{{data.air_Reward_policy}}</div>
