                       </ul>
                   </div>
               </div>
           </div>
           <scrollbar :das="scrollNode" v-if="qShow"></scrollbar>
        </div>
    </div>
</template>
<script>
    import scrollBar from '../component/scrollBarUltimate.vue';
    import airBox from '../template/airBox.vue';
    var data={ "success": {"airportDetailInfo":{"id":"26","airln_Cd":"十堰武当山机场","iATA":"WDS","iCAO":"ZHSY","cts_Cha_Nm":"中国","city_coordinate_j":"32.60","city_coordinate_w":"111.92","city_name":"2","air_port":"2","air_lvl":"4C","flg_type":"降B737—800、A320等系列机型","fire_lvl":"","air_ele":"","avg_tem":"","airline_data_state":"2","terrain_data_state":"2","air_subway":"","air_bus":"","air_distance":"15","air_covers":"186","air_terminal":"1","slots_total":"","terminal":"1","air_runway":"1","air_runway_long":"2600","air_runway_wide":"","air_apron":"","air_boarding_bridge":"","air_capacity":"","yeah_air":"2017","air_complete":"388,923","air_Passenger_throughput":"388923","air_Goods_throughput":"","air_line":"12","air_line_domestic":"12","air_line_inter":"0","air_early_date":"","air_acceptance_date":"","air_school_fly_date":"","air_flight_date":"","air_Planned_shipping_time":"","air_target_throughput":"","air_Preliminary_planning_and_city":"","air_Reward_policy":"","employee_id":1,"date":"2017-03-08 23:58:07"},"throughPut":{"iata":"wds","this_year":"2016","last_year":"2015","this_goods":"","last_goods":"","goods_ranking":"211","goods_compare":"","this_passenger":"388923","last_passenger":"","passenger_ranking":"126","passenger_compare":"","this_flights":"4726","last_flights":"","flights_ranking":"130","flights_compare":""}}};
    export default{
        data(){
            return{
                status:'',
                data:'',
                dataYear:'',
                selectData:"",
                selectCode:"",
                guolv:"",
                airport:'',
                scrollNodeX:{
                    parent:"cityBox8",
                    child:"cityCont8",
                    courseN:"course8",
                    sliderN:"slider8",
                    speed:60
                },
                scrollNode:{
                    parent:"airScroll",
                    child:"airScrollB",
                    courseN:"course1",
                    sliderN:"slider1",
                    speed:60
                },
                shos:0,
                unpack2:0,
                qShow:0
            }
        },
        components:{
            scrollbar:scrollBar,
            airBox:airBox,
            scrollBar8:scrollBar
        },
        props:['close'],
        watch:{
           close:function () {
               this.shos=0;
           },
           selectData:function () {
                let arr=[];
                let ley=this.selectData.toUpperCase();
                for(var key in this.airport){
                    this.airport[key].forEach((val)=>{
                        if(val.airportName.toUpperCase().indexOf(ley)!=-1||val.pinyin.toUpperCase().indexOf(ley)!=-1||val.py.toUpperCase().indexOf(ley)!=-1||val.code.toUpperCase().indexOf(ley)!=-1||val.city.toUpperCase().indexOf(ley)!=-1||val.citycode.toUpperCase().indexOf(ley)!=-1){
                            arr.push({
                                airportName:val.airportName,
                                code:val.code,
                                py:val.py
                            })
                        }
                        if(val.airportName!=''&&val.airportName!=undefined){
                            if(val.airportName.toUpperCase().indexOf(ley)!=-1){
                                arr.push({
                                    airportName:val.airportName,
                                    code:val.code,
                                    py:val.py
                                });
                                return
                            }
                        }else if(val.pinyin!=''&&val.pinyin!=undefined){
                            if(val.pinyin.toUpperCase().indexOf(ley)!=-1){
                                arr.push({
                                    airportName:val.airportName,
                                    code:val.code,
                                    py:val.py
                                });
                                return
                            }
                        }else if(val.py!=''&&val.py!=undefined){
                            if(val.py.toUpperCase().indexOf(ley)!=-1){
                                arr.push({
                                    airportName:val.airportName,
                                    code:val.code,
                                    py:val.py
                                });
                                return
                            }
                        }else if(val.citycode!=''&&val.py!=undefined){
                            if(val.py.toUpperCase().indexOf(ley)!=-1){
                                arr.push({
                                    airportName:val.airportName,
                                    code:val.code,
                                    py:val.py
                                });
                                return
                            }
                        }
                    });
                }
                this.guolv=arr;
           }
        },
        methods:{
            queryData:function (val) {
                if(this.selectCode!=""){
                    $.ajax({
                        type:'get',
                        url : 'restful/getAirportDetailInfoByCode',
                        data:{
                            code:this.selectCode
                        },
                        dataType : 'jsonp',
                        success:(data)=>{
                            this.data=data.success.airportDetailInfo;
                            this.dataYear=data.success.throughPut;
                            let status={
                                chd:[0,0,0],
                                jhd:[0,0],
                                fhd:[0,0,0],
                                qhd:[0,0,0,0,0],
                                uhd:[0,0,0],
                                xhd:[0,0,0],
                            };
                            if(this.data.air_state!=''&&this.data.air_state!=undefined){
                                status.chd[this.data.air_state]=1;
                            }
                            if(this.data.air_type!=''&&this.data.air_type!=undefined){
                                status.chd[this.data.air_type]=1;
                            }
                            if(this.data.air_port!=''&&this.data.air_port!=undefined){
                                status.chd[this.data.air_port]=1;
                            }
                            if(this.data.air_conditions!=''&&this.data.air_conditions!=undefined){
                                status.chd[this.data.air_conditions]=1;
                            }
                            if(this.data.airline_data_state!=''&&this.data.airline_data_state!=undefined){
                                status.chd[this.data.airline_data_state]=1;
                            }
                            if(this.data.terrain_data_state!=''&&this.data.terrain_data_state!=undefined){
                                status.chd[this.data.terrain_data_state]=1;
                            }
                            this.status=status;
                        },
                    });
                }
            },
            res:function (val) {
                this.selectCode=val.code;
                this.selectData=val.airportName;
                this.shos=0;
                this.unpack2=0;
            },
            input:function () {
                this.shos=0;
                this.unpack2=1;
            },
            reveal:function () {
                this.shos=1;
                this.unpack2=0;
            },
            queryMore:function(){
                this.qShow=1;
            },
            receive:function (val) {
                this.selectCode=val.code;
                this.selectData=val.airportName;
                this.shos=0;
            }
        },
        mounted(){
            this.airport=JSON.parse(sessionStorage.getItem('airportData'));
        },
    }
</script>
<style scoped>
    .innovationMin{
        height: 470px !important;
    }
    .labelx{
        width: 140px;
    }
    .inn-fNs{
        width: 650px !important;
    }
    .airContent,.airBox{
        height: auto !important;
    }
    .airBox{
        max-height: 230px;
        overflow: hidden;
    }
    .cityBox-item{
        font-size: 12px;
        color: white;
        height: 23px;
        line-height: 23px;
        padding-left: 20px;
    }
    .cityBox-item:hover{
        background-color:#25406d;
        color: #be5428;
        cursor:pointer;
        transition: background .3s linear;
        background: -webkit-gradient(linear, 0% 0%, 0% 100%,from(#224b79), to(#263763));
        background: -moz-linear-gradient(top, #224b79, #263763);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#224b79', endColorstr='#263763');
    }
    .cityBox-cont{
        position: relative;
    }
</style>


